<template>
  <div>
		<loading :active.sync="isLoading"></loading>
    <div class="mt-3 mb-3 text-right">
      <button
        class="btn btn-primary" @click="openModal(true)"
      >Create Coupon</button>
    </div>
    <div class="table-responsive">
      <table class="table table-striped">
        <thead>
          <tr>
            <th>Title</th>
            <th width="130">Enable/Disable</th>
            <th width="100">Percent</th>
            <th width="100">Due date</th>
            <th width="100">Code</th>
            <th width="180">Edit</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in coupons" :key="index">
            <td scope="row">{{item.title}}</td>
            <td>
							<span v-if="item.is_enabled" class="text-success">Enable</span>
							<span v-else class="text-warning">Disable</span>
						</td>
            <td>{{item.percent}}</td>
            <td>{{item.due_date}}</td>
            <td>{{item.code}}</td>
            <td>
              <button class="btn btn-outline-primary mr-2" @click="openModal(false,item)">Edit</button>
              <button class="btn btn-outline-danger" @click="openDelteModal(item.id)">Delete</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div
      class="modal fade"
      id="staticBackdrop"
      data-backdrop="static"
      tabindex="-1"
      role="dialog"
      aria-labelledby="staticBackdropLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header bg-primary text-white">
            <h5 class="modal-title" id="staticBackdropLabel">
              <span>新增優惠券</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row justify-content-center">
              <div class="col-md-8">
                <div class="form-group">
                  <label for="title">標題</label>
                  <input v-model="tempCoupon.title" type="text" id="title" class="form-control" placeholder="請輸入優惠券標題" />
                </div>
                <div class="form-group">
                  <label for="code">優惠碼</label>
                  <input v-model="tempCoupon.code" type="text" id="code" class="form-control" placeholder="請輸入優惠碼" />
                </div>
                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="percent">折扣</label>
                    <input v-model="tempCoupon.percent" type="text" id="percent" class="form-control" placeholder="請輸入%數" />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="duedate">到期日</label>
                    <input v-model="tempCoupon.due_date" type="text" id="duedate" class="form-control" placeholder="請輸入到期日" />
                  </div>
                </div>
                <div class="form-group">
                  <div class="form-check">
                      <input
												v-model="tempCoupon.is_enabled"
                        type="checkbox"
                        class="form-check-input"
                        id="isEnable"
                        :true-value="1"
                        :false-value="0"
                      />  
                    <label for="isEnable" class="form-check-label">
											是否啟用
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" @click="updateCoupon">確認</button>
          </div>
        </div>
      </div>
    </div>
		<div class="modal" id="deleteModal" tabindex="-1" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Warning Message</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <p>You want to delete the item ?</p>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" @click="deleteCoupon(deleteProductId)">確認</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>





<script>
import $ from "jquery"
export default {
	data() {
		return {
			tempCoupon: {},
			isLoading: false,
			coupons: [],
			pagination: {},
			deleteProductId: '',
			isNew: false,
		}
	},
	created() {
		console.log("Coupon view created");
		this.getCoupon();
	},
	methods: {
		openModal(isNew,item) {
			if (isNew) {
				this.tempCoupon = Object.assign({},{});
				this.isNew = isNew;
			} else {
				this.tempCoupon = Object.assign({},item);
				this.isNew = isNew;
			}
			$("#staticBackdrop").modal('show');
		},
		getCoupon(page = 1) {
			const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/admin/coupons?page=${page}`;
      console.log("Get coupons of page " + page);
      console.log(api);
      var vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        console.log("response data:");
				console.log(response);
				vm.coupons = response.data.coupons;
				vm.pagination = response.data.pagination;
				vm.coupons.forEach(function(item,index){
					var date = new Date(item.due_date * 1000);
					var dataValues =date.toISOString().split('T');
					//console.log(dataValues[0]);
					item.due_date = dataValues[0];
				})
        vm.isLoading = false;
      });

		},
		updateCoupon() {
			var api = this.isNew ? `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/admin/coupon`
													: `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/admin/coupon/${this.tempCoupon.id}`;
			var httpMethod = this.isNew ? 'post' : 'put';
      console.log("Update coupon");
			console.log(api);
			console.log(httpMethod)
      var vm = this;
			vm.isLoading = true;

			var tmpObj = Object.assign({},vm.tempCoupon);
			const dateTime = new Date(tmpObj.due_date).getTime();
			const timestamp = Math.floor(dateTime/1000);
			tmpObj.due_date = timestamp;
			console.log(timestamp);
		 	this.$http[httpMethod](api,{data:tmpObj}).then(response => {
        console.log("response data:");
				console.log(response);
				if (response.data.success) {
					console.log("Add coupon success");
					$("#staticBackdrop").modal('hide');
					vm.getCoupon();
				} else {
					console.log("Add coupon fail")
					$("#staticBackdrop").modal('hide');
				}
				vm.isLoading = false;	
      });
		},
		openDelteModal(id) {
			this.deleteProductId = id;
			console.log("Begin to delete coupon id : " + id);
      $("#deleteModal").modal("show"); 
		},
		deleteCoupon(id) {
			const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/admin/coupon/${id}`;
      console.log("Delete coupon");
      console.log(api);
      var vm = this;
			vm.isLoading = true;
			this.$http.delete(api).then(response => {
        console.log("response data:");
        console.log(response);
        if (response.data.success) {
          $("#deleteModal").modal("hide");
          vm.getCoupon();
        } else {
          $("#deleteModal").modal("hide");
          vm.getCoupon();
          console.log("Failure to delete product");
          vm.$bus.$emit("message:push", response.data.message, "danger");
				}
				vm.deleteProductId = '';
				vm.isLoading = false;
      });
		},
	},
}
</script>
